import React, { Component } from "react";
 
class home extends Component {
  render() {
    return (
		<div class="jumbotron">
		<h1>Description: </h1>      
			<h4>This visualization shows the output from an NLP model that imitates binary coding of content analysis. Each row represents a content feature, and the chart shows the probability of each data point (e.g., a comment) containing the content feature. 
			Unlike human binary decision (0=not having the feature, 1=having the feature), this AI support provides a probability score between 0 and 1. The human analyst can decide the cutting-off threshold and the optimal model depending upon a desired confidence level.
			</h4>
		</div>
    );
  }
}
 
export default home;